<template>
    <div class="home-page">
        <PageTitle icon="fa fa-home" main="Dashboard" sub=""></PageTitle>
    </div>
    <div class="col-12">
        <div class="col-12" v-for="(recipes, r) in recipes" :key="r">
            {{ recipes.name }}
        </div>
    </div>
</template>

<script>
import PageTitle from '@/components/template/PageTitle.vue'
import { baseApiUrl } from '@/global';
import axios from 'axios';
//import toastMsgs from '@/config/msgs'

export default {
    name: 'HomePage',
    components: {PageTitle},
    data: function() {
        return {
            typeCategory: 'all',
            recipes: []
        }
    },
    methods: {
        loadRecipes() {
            const url = `${baseApiUrl}/recipes`
            axios.get(url).then(res => {
                this.recipes = res.data
            })
        },
        reset() {
            this.mode = 'all',
            this.recipes = [],
            this.loadTypes()
        },
    },
    mounted() {
        this.loadRecipes()
    }
}
</script>

