<template>
    <div class="admin-user-list">
        <h1>Lista de usuários</h1>
        <table ref="tableUsers" class="table table-striped">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Nome</th>
              <th scope="col">Email</th>
              <th scope="col">CPF</th>
              <th scope="col">Telefone</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(users, u) in users" :key="u">
              <td>{{ users.id }}</td>
              <td>{{ users.name }}</td>
              <td>{{ users.email }}</td>
              <td>{{ users.phone }}</td>
              <td>{{ users.cpf }}</td>
            </tr>
          </tbody>
        </table>
    </div>
</template>

<script>
import { baseApiUrl } from '@/global';
import axios from 'axios';

export default {
    name: 'AdminUserList',
    data: function() {
      return {
        users: []
      }
    },
    methods: {
        loadUsers() {
            const url = `${baseApiUrl}/users`
            axios.get(url).then(res => {
                this.users = res.data
            })
        }
    },
    mounted() {
        this.loadUsers()
    }
    
}

</script>

