<template>
    <div class="card-recipe">
        <div class="col-lg-3 col-md-6">
            <div class="card" typeof="width: 18rem;">
                <img :src="previewImage" class="card-img-top" alt="">
                <div class="card-body">
                    <h4 class="card-title">{{ title }}</h4>
                    <p class="card-text">{{ description }}</p>
                    <div class="card-body-buttons">
                        <a href="#" class="btn btn-outline-primary"><i class="fa fa-eye" aria-hidden="true"></i></a>
                        <a href="#" class="btn btn-outline-warning"><i class="fa fa-pencil"></i></a>
                        <a href="#" class="btn btn-outline-danger"><i class="fa fa-trash" aria-hidden="true"></i></a>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CardRecipeTemplate',
    data: function() {
        return {
            previewImage: "data:image/png;base64," + this.image,
        }
    },
    props: {
        title: String,
        description: String,
        image: String
    },
}
</script>

<style>
    .card-img-top {
        border-radius: 50px;
        padding: 20px;
        height: 250px;
        width: 100%;
        object-fit: cover;
    }

    .card {
        border-radius: 30px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
    }

    .card-body {
        padding: 25px;
        margin-top: -25px;
    }

    .card-body-buttons {
        text-align: center;
    }

    .card-body .btn {
        
        margin: 2px;
        padding: 10px;
        width: 30%;
    }
</style>