<template>
    <div class="menu flex-shrink-0 p-3" v-show="isMenuVisible">
        <ul class="nav nav-pills flex-column mb-auto">
            <li class="nav-item">
                <RouterLink class="nav-link" to="/recipespage">
                    <span class="h4 align-middle"> Lista Geral</span>
                </RouterLink>
            </li>
            <li class="nav-item">
                <RouterLink class="nav-link" to="/yourlistrecipespage">
                    <span class="h4 align-middle"> Suas Receitas</span>
                </RouterLink>
            </li>
            <li class="nav-item">
                <a href="#" class="nav-link collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                    <span class="h4"> Tipos</span></a>
                <div class="collapse" id="home-collapse">
                    <ul class="btn-toggle-nav list-unstyled">
                        <div class="col-12" v-for="(types, t) in types" :key="t">
                            <li>
                                <a href="#" class="nav-link rounded pad-10"> <span>{{ types.name }}</span></a>
                            </li>
                        </div>
                    </ul>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import { baseApiUrl } from '@/global';
import axios from 'axios';

export default {
    name: 'MenuTemplate',
    computed: mapState(['isMenuVisible']),
    data: function() {
        return {
            isActive: true,
            types: []
        }
    },
    methods: {
        loadTypes() {
            const url = `${baseApiUrl}/types`
            axios.get(url).then(res => {
                this.types = res.data
            })
        },
        reset() {
            this.mode = 'save',
            this.recipe = {},
            this.loadRecipes()
        },
    },mounted() {
        this.loadTypes()
    }
}
</script>

<style>
    .menu {
        grid-area: menu;
        background: linear-gradient(180deg, rgba(11,10,33,1) 0%, rgba(48,48,48,1) 100%);
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
    
    .menu ul li span{
        color: white;
        font-size: 1.2rem;
        font-weight: 100;
        flex-grow: 1;
        transition: background-color 0.3s ease;
    }

    .nav-link :hover {
        color: black;
    }
    
    .pad-10 {
        padding-left: 10%;
    }
</style>