<template>
    <div class="recipes-page">
        <PageTitle icon="fa fa-cutlery" main="Lista de Receitas" sub=""></PageTitle>
    </div>
    <div class="col-12">
        <div class="col-12" v-for="(recipes, r) in recipes" :key="r">
            {{ recipes.name }}
        </div>
    </div>
</template>

<script>
import PageTitle from '@/components/template/PageTitle.vue'
import { baseApiUrl } from '@/global';
import axios from 'axios';

export default {
    name: 'RecipesPage',
    components: {PageTitle},
    data: function() {
        return {
            mode: 'save',
            recipe: {},
            recipes: []
        }
    },
    methods: {
        loadRecipes() {
            axios.get(`${baseApiUrl}/recipes`).then(res => {
                this.recipes = res.data
            })
        },
        reset() {
            this.mode = 'save',
            this.recipe = {},
            this.loadRecipes()
        },
        loadRecipe(recipe, mode = 'save') {
            this.mode = mode
            this.recipe.id = recipe.id
            this.recipe.name = recipe.name
        }
    },
    mounted() {
        this.loadRecipes()
    }
}
</script>

